import React from "react";
import { Switch, Route } from "react-router-dom";

import Home from "./components/Home";
import YaraBela from "./components/YaraBela";

const App = () => (
  <Switch>
    <Route path="/pwa-test-project/" exact component={Home} />
    <Route path="/pwa-test-project/bela" component={YaraBela} />
    <Route path="/pwa-test-project/mila" component={YaraBela} />
  </Switch>
)
export default App;



